h1 {
  color: red
}
.container{
    width: 600px; 
    margin: 0 auto; 

}

.card{
    margin: 0 auto;
}
.navbar-brand {
  display: block;
  width: 100%;
  text-align: center;
  margin: 0;
  padding-bottom: 5px
}

.navbar {
  background: #6B8E23; 
}

.navbar a {
  color: white
}

.navbar i {
  vertical-align: middle;
  padding-right: 5px
}

.nav li {
  margin-right: 25px
}


.wrapper {
  padding-top: 50px;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

